        </el-tag>
        <el-input
          class="input-new-tag"
          v-if="inputVisibleA"
          v-model="inputValueA"
          ref="saveTagInputA"
          size="small"
          @keyup.enter.native="handleInputConfirmA"
          @blur="handleInputConfirmA"
        >
        </el-input>
         <el-tooltip  placement="right">
          <div slot="content"><br/></div>
        <el-button v-if="seen" class="button-new-tag"  size="small" @click="showInputA" >+ </el-button>
         </el-tooltip>
        &nbsp;&nbsp;&nbsp;<span v-if="seen2">:{{value2}}</span>
      </span></el-col>
      <el-col :span="4">&nbsp;</el-col>
    </el-row>

    <el-row style="text-align: left;margin-top: 20px;" v-if="eyeSeen">
      <el-col :span="4">&nbsp;</el-col>
      <el-col :span="16"><span>:{{dynamicTagsA[0]}}

        &nbsp;&nbsp;&nbsp;<span v-if="seen2">:</span>{{value2}}
      </span></el-col>
      <el-col :span="4">&nbsp;</el-col>
    </el-row>

    <el-row style="margin-top: 20px;height: 40px;line-height: 40px" v-if="beforeSeen">
      <el-col :span="4">&nbsp;</el-col>

      <el-col :span="16" style="text-align: left;height: 60px;line-height: 60px;border-top: 1px solid lightgray;
      border-bottom:1px solid lightgray;">
        <!--<el-row>-->
          <!--<el-col :span="6"><el-button @click="openFullScreen"></el-button></el-col>-->
          <!--<el-col :span="6"><el-button @click="backToUndiagnosed"></el-button></el-col>-->
          <!--<el-col :span="6"><el-button @click="finishAndCreatePdf"></el-button></el-col>-->
          <!--<el-col :span="6"><el-button @click="onlyBack"></el-button></el-col>-->
        <!--</el-row>-->

        <div class="button_area">
          <!--<div><el-button @click="openFullScreen"></el-button></div>-->
          <div><el-button @click="clickCreateImgBtn"></el-button></div>
          <div><el-button @click="backToUndiagnosed"></el-button></div>
          <div><el-button @click="finishAndCreatePdf"></el-button></div>
          <div><el-button @click="onlyBack"></el-button></div>
        </div>
      </el-col>

      <el-col :span="4"></el-col>
    </el-row>

    <el-row style="margin-top: 20px;height: 40px;line-height: 40px" v-if="finishSeen">
      <el-col :span="4">&nbsp;</el-col>

      <el-col :span="16" style="text-align: left;height: 60px;line-height: 60px;border-top: 1px solid lightgray;
      border-bottom:1px solid lightgray;">
        <el-row>
          <el-col :span="8"><el-button @click="onlyBack"></el-button></el-col>
          <el-col :span="8"><el-button @click="createReport"></el-button></el-col>
          <el-col :span="8">&nbsp;</el-col>
        </el-row>
      </el-col>

      <el-col :span="4"></el-col>
    </el-row>

  </el-main>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      :show-close="false"
      width="30%">
      <div class="block">
        <el-date-picker
          v-model="value2"
          align="right"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder=""
          :picker-options="pickerOptions">
        </el-date-picker>
      </div>
      <span slot="footer" class="dialog-footer">
<!--    <el-button @click="cancelSelectDate"> </el-button>-->
    <el-button type="primary" @click="confirmSelectDate"> </el-button>
  </span>
    </el-dialog>


    <el-dialog
      title=""
      :visible.sync="uplevelVisible"
      width="30%">
      <div class="block">
        <el-form>
        <el-form-item label="">
          <el-input
            v-model="uplevelHospital"
            auto-complete="off"
            placeholder="(20)"
            maxlength="20"
            >
          </el-input>
        </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
    <el-button @click="cancelInputHospital"> </el-button>
    <el-button type="primary" @click="confirmInputHospital"> </el-button>
  </span>
    </el-dialog>

<!--    <el-dialog title="" :visible.sync="uplevelVisible">-->
<!--      <el-form-item label=""  style="width: 30%">-->
<!--        <el-input v-model="uplevelValue" auto-complete="off"></el-input>-->
<!--      </el-form-item>-->
<!--    </el-dialog>-->




    <el-dialog
      title=""
      :visible.sync="centerDialogVisible"
      width="30%"
      center>
      <span></span>
      <span slot="footer" class="dialog-footer">
<!--    <el-button @click="centerDialogVisible = false"> </el-button>-->
    <el-button type="primary" @click="centerDialogVisible = false"> </el-button>
  </span>
    </el-dialog>

  </el-container>

</template>

<script>
  import store from '@/vuex/store'
  import axios from 'axios'
  import url from "../../static/js/hostUrl";
  import cookie from "../../static/js/cookie";
  import {user_diagnose, createImg} from '../../api/api';
  let host = url.url;
  // window.onresize=function () {
  //   /*div46px46px*/
  //   let leftNavHeight = window.innerHeight-46;
  //   document.getElementById('aside_id').style.height = leftNavHeight+'px';
  // };

  export default {
    data() {
      return {
        jwsDisplayInfo:'',
        warning:'',
        warningShow:false,
        hospital_name:'',
        patient_info:{},
        restaurants: [],
        AIdata:{},
        state: '',
        timeout:  null,
        suggestions:['','',','],
        diseases:['','','','',
                  '',''],
        fits: ['fill',],
        right_src: 'https://www.orbisbiotech.cn/media/noimg.jpg',
        left_src: 'https://www.orbisbiotech.cn/media/noimg.jpg',
        right_srcList: [],
        left_srcList: [],
        dynamicTagsR: [], /**/
        dynamicTagsL: [], /**/
        dynamicTagsA:[],  /**/
        diaStrR:'',       /**/
        diaStrL:'',       /**/
        diaStrA:'',       /**/
        finishSeen:false, /**/
        beforeSeen:true,  /**/
        inputVisibleR: false,
        inputVisibleL: false,
        inputVisibleA: false,
        leftEye:false,
        rightEye:false,
        advice:false,
        inputValueR: '',
        inputValueL: '',
        inputValueA: '',
        seen:true,
        seen2:false,
        eyeSeen:false,
        diaSeen:true,
        activeColor1:'azure',
        activeColor2:'azure',
        dialogVisible: false,
        uplevelVisible:false,
        uplevelHospital:'',
        fullscreenLoading: false,
        centerDialogVisible: false,
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() < Date.now();
          },
          shortcuts: [{
            text: '',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        value1: '',
        value2: '',
        strengthen:false,
      };
    },
    methods: {
      /**/
      handleCloseR(tag) {
        this.dynamicTagsR.splice(this.dynamicTagsR.indexOf(tag), 1);
      },
      /**/
      showInputR() {
        this.activeColor2 = 'lightblue';
        this.inputVisibleR = true;
        this.rightEye = true;
        this.leftEye = false;
        this.$nextTick(_ => {
          this.$refs.saveTagInputR.$refs.input.focus();
        });
      },
      /**/
      handleInputConfirmR() {

        this.activeColor2 = 'azure';
        let inputValueR = this.inputValueR;
        if (inputValueR) {
          this.dynamicTagsR.push(inputValueR);
        }
        this.inputVisibleR = false;
        this.inputValueR = '';
      },

      /**/
      handleCloseL(tag) {
        this.dynamicTagsL.splice(this.dynamicTagsL.indexOf(tag), 1);
      },
      /**/
      showInputL() {
        this.activeColor2 = 'lightblue';
        this.inputVisibleL = true;
        this.rightEye = false;
        this.leftEye = true;
        this.$nextTick(_ => {
          this.$refs.saveTagInputL.$refs.input.focus();
        });
      },
      /**/
      handleInputConfirmL() {
        this.activeColor2 = 'azure';
        let inputValueL = this.inputValueL;
        if (inputValueL) {
          this.dynamicTagsL.push(inputValueL);
        }
        this.inputVisibleL = false;
        this.inputValueL = '';
      },

      /**/
      handleCloseA(tag) {
        this.dynamicTagsA.splice(this.dynamicTagsA.indexOf(tag), 1);
        this.seen = true;
        this.seen2 = false;
        this.value2='';
      },
      /**/
      showInputA() {
        this.leftEye=false;
        this.rightEye=false;
        this.activeColor1 = 'lightblue';
        this.inputVisibleA = true;
        this.advice = true;
        this.$nextTick(_ => {
          this.$refs.saveTagInputA.$refs.input.focus();
        });
      },
      /**/
      handleInputConfirmA() {
        this.activeColor1='azure';
        let inputValueA = this.inputValueA;
        let dynamicTagsA = this.dynamicTagsA;
        if (inputValueA) {
          this.dynamicTagsA.push(inputValueA);
        }
        if(dynamicTagsA.length !== 0 ){
          this.seen = false;
        }
        this.inputVisibleA = false;
        this.inputValueA = '';
      },
      /**/
      addDiagnose(index){
        if(this.leftEye){
          this.dynamicTagsL.push(index);
          let tem_dynamicTagsL = this.duplicateRemove(this.dynamicTagsL);  /**/
          this.dynamicTagsL = tem_dynamicTagsL;
          this.handleInputConfirmL();
        }else if(this.rightEye){
          this.dynamicTagsR.push(index);
          let tem_dynamicTagsR = this.duplicateRemove(this.dynamicTagsR);  /**/
          this.dynamicTagsR = tem_dynamicTagsR;
          this.handleInputConfirmR();
        }
      },
      /**/
      addAdvice(index){
        let dynamicTagsA = this.dynamicTagsA;
        if(this.advice){
          if(dynamicTagsA.length !==0){
            return
          }else{
            this.dynamicTagsA.push(index);
            this.handleInputConfirmA();
          }
          if(index===''){
            this.dialogVisible=true;
            // this.seen2=true;
          }
          if(index===''){
            this.uplevelVisible=false;  //falsetrue
          }
        }
      },

      formatSex: function (row) {
        return row.patient_gender === "0" ? '' : row.patient_gender === "1" ? '' : '';
      },

      /**/
      finishDiagnose(){
          let diaStrR = this.dynamicTagsR.toString();  /**/
          let diaStrL = this.dynamicTagsL.toString();  /**/
          let diagnose_advice = this.dynamicTagsA[0].toString(); /**/
          if(diaStrR.length>40||diaStrL.length>40||diagnose_advice.length>40){
            this.$alert('40', '', {
              confirmButtonText: '',
            });
            return false;
          }

          this.diaStrR = diaStrR;
          this.diaStrL = diaStrL;
          this.submitDiagnose();  /**/
          this.eyeSeen = true;
          this.diaSeen = false;
          this.finishSeen = true;
          this.beforeSeen = false;
      },

      /**/
      openFullScreen() {
        if(this.dynamicTagsA.length===0){
          this.open(); /**/
          return
        }else{
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        setTimeout(() => {
          loading.close();
          this.finishDiagnose();
        }, 500);
        }
      },

      /**/
      clickCreateImgBtn:function(){
          const loading = this.$loading({
            lock: true,
            text: 'Loading',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });

          let patient_id = this.patient_info.patient_id;
          let temp_data = this.getDiagnoseResult();

          let right_origin_src = temp_data['other_data']['right_origin_src'];
