<template>
  <div class="undiagnosed" id="diagnosedPage">
    <el-row>
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/' }">PACS</el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>

    <el-row class="operationNew">
      <el-col v-bind:class="{ active: isActive1 }" :span="2"><a @click="turnBlue" id="isActive1" href="javascript:;"></a></el-col>
      <el-col v-bind:class="{ active: isActive4 }" :span="3" class="timepicker"><a @click="" id="isActive4" href="javascript:;"><el-date-picker
        v-model="dateValue"
        align="right"
        type="date"
        @change="selectDate"
        placeholder=""
        :picker-options="pickerOptions">
      </el-date-picker></a></el-col>
      <el-col :span="6">&nbsp;</el-col>
      <el-col :span="4"><el-button type="primary" @click="batchPdfGen"><i class="el-icon-printer el-icon--right"></i></el-button></el-col>
      <div style="text-align: center;">
        <el-col :span="2"><a href="javascript:;" v-on:click="refresh"><i class="icon iconfont icon-shuaxin1"></i></a>
          <a style="display:none;" id="downloadzip" href="" download="Re.zip">Download file</a>
        </el-col>
        <el-col :span="5"><el-input
          v-model="inputVal"
          placeholder="// ">
        </el-input></el-col>
        <el-col :span="2"><el-button type="primary" @click="searchBtn" icon="el-icon-search"></el-button></el-col>
      </div>
    </el-row>

    <el-row class="dataZone">
      <template>
        <el-table
          ref="multipleTable"
          :data="tableData"
          stripe
          style="width: 100%"
          @selection-change="handleSelectionChange">
          <el-table-column
            type="selection"
            width="55">
          </el-table-column>
<!--          <el-table-column-->
<!--            prop="patient_id"-->
<!--            label=""-->
<!--            >-->
<!--          </el-table-column>-->
          <el-table-column
            prop="patient_name"
            label=""
            >
          </el-table-column>
          <el-table-column
            prop="patient_gender"
            label=""
            :formatter="formatSex">
          </el-table-column>
          <el-table-column
            prop="patient_age"
            label=""
           >
          </el-table-column>
          <el-table-column
            prop="patient_tel"
            label=""
            >
          </el-table-column>
          <el-table-column
            prop="apply_time"
            label=""
          :formatter="formatTime">
          </el-table-column>
          <el-table-column
            prop="status"
            label=""
            :formatter="formatStatus">
          </el-table-column>
          <el-table-column
            label="">
            <template slot-scope="scope">
              <el-tooltip content="" placement="left">
              <el-button @click="handleView(scope.$index, scope.row)" type="primary" size="small" icon="el-icon-view" circle></el-button>
              </el-tooltip>

              <el-tooltip content="" placement="right">
              <el-button @click="handleEdit(scope.$index, scope.row)" type="primary" size="small" icon="el-icon-edit" circle></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </el-row>
    <el-row class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[5,10]"
        :page-size="page_size"
        :pager-count="7"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </el-row>
    <!---->
    <el-dialog title="" :visible.sync="editFormVisible" v-model="editFormVisible" :close-on-click-modal="false">
      <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
        <el-form-item label="" prop="patient_name" style="width: 25%">
          <el-input v-model="editForm.patient_name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="patient_tel">
          <el-input type="text" placeholder="" oninput="value=value.replace(/[^\d]/g,'')" maxlength="11" show-word-limit v-model="editForm.patient_tel" style="width: 25%"></el-input>
        </el-form-item>
        <el-form-item label="" prop="patient_age">
          <el-input-number v-model="editForm.patient_age" oninput="value=value.replace(/[^\d]/g,'')" :min="0" :max="200" style="width: 25%"></el-input-number>
        </el-form-item>
        <el-form-item label="" prop="ID_number">
          <el-input type="text" placeholder=""  oninput="value=value.replace(/[\W]/g,'')" maxlength="18" show-word-limit v-model="editForm.ID_number" style="width: 25%"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="editForm.patient_gender" :onclick="handlePregnant()">
            <el-radio class="radio" :label="'0'"></el-radio>
            <el-radio class="radio" :label="'1'"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <template>
            <el-radio-group v-model="radio" :onchange="editjws()">
              <el-radio :label="3"></el-radio>
              <el-radio :label="6"></el-radio>
            </el-radio-group>
          </template>

          <br/>
          <el-form-item v-if="editFormjwsVisible">
            <el-checkbox v-model="editFormJwsForm.DM_year"></el-checkbox>
            <el-checkbox v-model="editFormJwsForm.HTN_year"></el-checkbox>
            <el-checkbox v-model="editFormJwsForm.HPL_year"></el-checkbox>
<!--            <span>:</span><el-input-number v-model="editFormJwsForm.DM_year" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>-->
<!--            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;-->
<!--            <span>:</span><el-input-number v-model="editFormJwsForm.HTN_year" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>-->
<!--            <br/>-->
<!--            <span>:</span><el-input-number v-model="editFormJwsForm.HPL_year" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>-->
<!--            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;-->
            <span>&nbsp;&nbsp;&nbsp;:</span><el-input-number v-bind:disabled="pregnant_seen" v-model="editFormJwsForm.pregnant_weeks" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>
            <br/>
            <span>:&nbsp;&nbsp;&nbsp;</span><el-input size="mini" style="width: 410px"
                                                        placeholder="(20)"
                                                        maxlength="20"
                                                        v-model="editFormJwsForm.other"
                                                        clearable>
          </el-input>
            <br/>
            <span>:</span>
            :<template>
            <el-select v-model="editFormLeftDiopterValue" size="mini" clearable  placeholder="" style="width: 160px">
              <el-option
                v-for="item in editFormLeftDiopter"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </template>
            &nbsp;&nbsp;&nbsp;&nbsp;:<template>
            <el-select v-model="editFormRightDiopterValue" size="mini" clearable  placeholder="" style="width: 160px">
              <el-option
                v-for="item in editFormLeftDiopter"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </template>
          </el-form-item>
        </el-form-item>
        <br/>
        <el-form-item label="" prop="address">
          <el-input type="textarea" v-model="editForm.address"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisible = false"></el-button>
        <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
      </div>
    </el-dialog>

    <!---->
    <!--mounted-->
    <el-carousel :interval="1000000"  height="800px" v-if="reportFormVisible"
                 style="width: 800px;position: fixed;z-index: 99;left: 30%;top: 1%;bottom:1%"
                 :loop="false"
                  @change="changeCarousel">
      <el-carousel-item v-for="(item,i) in this.reportForm.diagnose" :key="item.id">
        <el-row style="text-align: center;font-weight: bold;font-size: x-large;margin-top: 10px">
          <el-col :span="5">&nbsp;</el-col>
          <el-col :span="14" id="report"><span style="font-weight: unset;font-size: small;">(:{{i+1}}/{{reportForm.diagnose.length}})</span></el-col>
          <el-col :span="5" style="text-align: right">
            <el-tooltip content="" placement="left">
            <a href="javascript:;" @click="closeReport"><i class="el-icon-circle-close"></i></a>
            </el-tooltip>
            &nbsp;
