        <el-row style="margin-top: 20px">
          <el-col :span="1">&nbsp;</el-col>
          <el-col :span="5">:{{reportForm.patient_name}}</el-col>
          <el-col :span="5" v-if="reportForm.patient_gender==='0'">:</el-col>
          <el-col :span="5" v-else>:</el-col>
          <el-col :span="6">:{{reportForm.patient_age}}</el-col>
          <el-col :span="7">:{{reportForm.diagnose[i]["diagnose_time"]}}</el-col>
        </el-row>

        <el-row style="margin-top: 20px">
          <el-col :span="12" style="text-align: center">OD(:{{reportForm.diagnose[i]["iqa_score_right"]}})</el-col>
          <el-col :span="12" style="text-align: center">OS(:{{reportForm.diagnose[i]["iqa_score_left"]}})</el-col>
        </el-row>

        <el-row style="margin-top: 30px">
<!--          <el-col :span="12"><span>{{picList[i]['data'][0]}}</span></el-col>-->
          <el-col :span="12" style="text-align: center">
            <div class="demo-image__preview">
              <el-image
                id="right_origin_src"
                style="height: 250px;"
                :src="right_src"
                :preview-src-list="right_srcList">
              </el-image>
            </div>
          </el-col>
          <el-col :span="12" style="text-align: center">
            <div class="demo-image__preview">
              <el-image
                id="left_origin_src"
                style="height: 250px;"
