<script>
  import axios from 'axios'
  import {editPatientBasicInfo, get_patient_info, createImg} from '../../api/api';
  import cookie from "../../static/js/cookie";
  import url from "../../static/js/hostUrl";

  let host = url.url;
  export default {
    inject: ['reload'],
    name:'undiagnosed',
    created:function(){
      this.get_patients(1,10)
    },
    methods:{
      /**/
      reloadFun () {
        this.reload()
      },
      /**/
      refresh:function () {
        this.reloadFun();
        let p = 1;
        this.currentPage = 1;
        let page_size = this.page_size;
        let search  = this.inputVal;
        if(this.otherDate){
          this.get_patients_by_rules(p,page_size,this.dateValue,search);
          console.log("otherdate");
        }else{
          this.get_patients_by_rules(p,page_size,'',search);
          console.log("total");
        }
      },
      /**/
      handleSizeChange( val) {
        let page_size = val;
        this.page_size = page_size;
        this.currentPage = 1;
        let p = this.currentPage;
        let search = this.inputVal;
        console.log(p,page_size);
        if(this.otherDate){
          this.get_patients_by_rules(p,page_size,this.dateValue,search);
          console.log("otherdate");
        }else{
          this.get_patients_by_rules(p,page_size,'',search);
          console.log("page_size total");
        }
      },
      /**/
      handleCurrentChange(val) {
        let p  = val;
        let page_size = this.page_size;
        let search = this.inputVal;
        console.log(p,page_size);
        if(this.otherDate){
          this.get_patients_by_rules(p,page_size,this.dateValue,search);
          console.log("otherdate");
        }else{
          this.get_patients_by_rules(p,page_size,'',search);
          console.log("pagination total");
        }
      },

      turnBlue:function(e){
        let thisActive = e.currentTarget.id;
        if(thisActive === "isActive1"){
          console.log('isActive1');
          this.isActive1 = true;
          this.isActive2 = false;
          this.isActive3 = false;
          this.isActive4 = false;
          this.otherDate = false;
          this.dateValue = '';
          this.refresh();
        }
      },

      get_patients:function (p,size,apply_time,search) {
        let para = {};
        para['hospital_id'] = cookie.getCookie('hospital_id');
        para['p'] = p;
        para['page_size'] = size;
        para['ordering'] = '-apply_time';
        para['diagnose_record'] = 1;

        get_patient_info(para).then(response=>{
          console.log('response',response);
          this.tableData = response.results;
          this.totalNum = response.count;
        })
          .catch(error=>{
            console.log((error))
          })
      },
      get_patients_by_rules:function (p,size,apply_time,search) {
        let para = {};
        para['hospital_id'] = cookie.getCookie('hospital_id');
        para['p'] = p;
        para['page_size'] = size;
        para['apply_time'] = apply_time;
        para['search'] = search;
        para['ordering'] = '-apply_time';
        para['diagnose_record'] = 1;
        get_patient_info(para).then(response=>{
          console.log('response',response);
          this.tableData = response.results;
          this.totalNum = response.count;
        })
        // let hospital_id = cookie.getCookie('hospital_id');
        // axios.get(host+"/patients/?p="+p+"&page_size="+size+
        //   "&ordering=-apply_time"+"&apply_time="+apply_time+"&hospital_id="+hospital_id+"&search="+search+"&diagnose_record=1")
        //   .then(response=>{
        //     console.log((response))
        //     this.tableData = response.data.results
        //     this.totalNum = response.data.count
        //   })
        //   .catch(error=>{
        //     console.log((error))
        //   })
      },
      selectDate:function () {
        this.isActive1=false;
        this.isActive4=true;
        this.otherDate = true;
        let seperator1 = "-";
        let date = new Date(this.dateValue);
        let year = date.getFullYear();
        let month = date.getMonth() + 1;
        let strDate = date.getDate();
        if (month >= 1 && month <= 9) {
          month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
          strDate = "0" + strDate;
        }
        let selectdate = year + seperator1 + month + seperator1 + strDate;
        this.dateValue  = selectdate;
        this.currentPage = 1;
        let search = this.inputVal;
        console.log(this.dateValue);
        console.log(search);
        this.get_patients_by_rules(1,this.page_size,selectdate,search);
      },
      searchBtn:function () {
        console.log('searchBtn');
        this.currentPage = 1;
        this.get_patients_by_rules(1,this.page_size,this.dateValue,this.inputVal);
      },
      //
      formatSex: function (row) {
        return row.patient_gender === "0" ? '' : row.patient_gender === "1" ? '' : '';
      },
      //
      formatStatus: function (row) {
        return row.status === "0" ? '' : row.status === "2" ? '' : '';
      },
      //
      formatTime: function (row) {
        let lastOne = row.diagnose.length-1;
        let apply_time = row.diagnose[lastOne].apply_time;
        if(apply_time){
          return apply_time
        }else{
          return  row.diagnose[lastOne-1].apply_time
        }

      },
      //
      handleEdit: function (index, row) {
        let historyDisease = row['historyDisease'];
        console.log(historyDisease);
        this.editForm = Object.assign({}, row);
        this.editForm.isFront=true;  //
        if(historyDisease[0]){
          console.log('');
          this.editFormVisible = true;

          if(historyDisease[0]['DM_year']===1||historyDisease[0]['DM_year']===true){
            historyDisease[0]['DM_year']=true
          }
          if(historyDisease[0]['HPL_year']===1||historyDisease[0]['HPL_year']===true){
            historyDisease[0]['HPL_year']=true
          }
          if(historyDisease[0]['HTN_year']===1||historyDisease[0]['HTN_year']===true){
            historyDisease[0]['HTN_year']=true
          }
          if(historyDisease[0]['pregnant_weeks']===0){
            historyDisease[0]['pregnant_weeks']=''
          }

          this.editFormJwsForm = historyDisease[0];
          this.editFormLeftDiopterValue = historyDisease[0]['left_diopter'];
          this.editFormRightDiopterValue=historyDisease[0]['right_diopter'];
          this.editForm.jwsExist=1;  //
          this.editFormjwsVisible=true; //
          this.radio=3; //
        }else{
          console.log('');
          this.editFormVisible = true;
          this.editForm.jwsExist=0; //
          this.editFormjwsVisible=false; //
          this.radio=6;//
        }
      },

      editjws:function(){
        let radio = this.radio;
        console.log(radio);
        if(radio===3){
          this.editForm.jwsExist=1;
          this.editFormjwsVisible=true;
        }else if(radio===6){
          this.editFormjwsVisible=false;
          this.editFormJwsForm={};
          this.editFormRightDiopterValue='';
          this.editFormLeftDiopterValue='';
          this.editForm.jwsExist=0;
        }
      },
      handlePregnant:function(){
        let editGender = this.editForm.patient_gender;
        this.pregnant_seen = editGender === '0';
        // if(editGender==='0'){
        //   this.pregnant_seen=true;
        //   this.editFormJwsForm.pregnant_weeks='';
        // }else{
        //   this.pregnant_seen=false;
        //
        // }
      },

      //
      editSubmit: function () {
        this.$refs.editForm.validate((valid) => {
          if (valid) {
            this.$confirm('', '', {}).then(() => {
              this.editLoading = true;
              //NProgress.start();
              console.log(123456789);
              let jws = this.editFormJwsForm;
              console.log(this.editForm);
              let editInfo = {};
              if(this.editFormLeftDiopterValue!==''){
                jws['left_diopter'] = this.editFormLeftDiopterValue;
              }
              if(this.editFormRightDiopterValue!==''){
                jws['right_diopter'] = this.editFormRightDiopterValue;
              }
              editInfo['jwsInfo'] = jws;
              editInfo['basicInfo'] = this.editForm;

              console.log('13256',editInfo);
              let para = Object.assign({}, editInfo);
              console.log('para',para);
              // para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
              editPatientBasicInfo(para).then((res) => {
                this.editLoading = false;
                //NProgress.done();
                this.$message({
                  message: '',
                  type: 'success'
                });
                this.$refs['editForm'].resetFields();
                this.editFormVisible = false;
                this.refresh();
              });
            });
          }
        });
      },

      /**/
      getReportData:function(){
        let carouselIndex = this.carouselIndex;
        let result = this.getDiagnoseResult(this.reportForm.diagnose);
        let basic_data = {};
        let other_data = {};
        let total_data = {};
        basic_data['patient_id'] = this.reportForm.patient_id;
        basic_data['patient_name'] = this.reportForm.patient_name;
        basic_data['patient_gender'] = this.reportForm.patient_gender;
        basic_data['patient_age'] = this.reportForm.patient_age;
        basic_data['left_diagnose'] = this.reportForm.diagnose[carouselIndex].left_diagnose;
        basic_data['right_diagnose'] = this.reportForm.diagnose[carouselIndex].right_diagnose;
        basic_data['diagnose_time'] = this.reportForm.diagnose[carouselIndex].diagnose_time;
        let diagnose_advice = this.reportForm.diagnose[carouselIndex].diagnose_advice;
        let review_time = this.reportForm.diagnose[carouselIndex].review_time;
        if(review_time){
          diagnose_advice = ''+review_time+'';
          basic_data['diagnose_advice'] = diagnose_advice;
        }else{
          basic_data['diagnose_advice'] = diagnose_advice;
        }
        other_data['username'] = cookie.getCookie('name');
        let right_origin_src1 = document.getElementById('right_origin_src').src;
        let left_origin_src1 = document.getElementById('left_origin_src').src;
        let right_origin_src = right_origin_src1.split('media')[1];
        let left_origin_src = left_origin_src1.split('media')[1];
        other_data['right_origin_src'] = right_origin_src;
        other_data['left_origin_src'] = left_origin_src;
        //noimg
        if(other_data['right_origin_src'].search('noimg')===-1){
          let index= other_data['right_origin_src'].lastIndexOf(".");
          let format = other_data['right_origin_src'].substr(index);
          other_data['right_AI_src'] = other_data['right_origin_src'].replace(format,'_AI'+format);
        }else{other_data['right_AI_src'] = other_data['right_origin_src']}
        //noimg
        if(other_data['left_origin_src'].search('noimg')===-1){

          let index= other_data['left_origin_src'].lastIndexOf(".");
          let format = other_data['left_origin_src'].substr(index);

          other_data['left_AI_src'] = other_data['left_origin_src'].replace(format,'_AI'+format);
        }else{
          other_data['left_AI_src'] = other_data['left_origin_src'];
        }

        other_data['oozed_amount'] = result[carouselIndex].oozed_amount;
        other_data['bleeding_amount'] = result[carouselIndex].bleeding_amount;
        other_data['oozed_area'] = result[carouselIndex].oozed_area;
        other_data['bleeding_area'] = result[carouselIndex].bleeding_area;
        other_data['max_oozed_area'] = result[carouselIndex].max_oozed_area;
        other_data['max_bleeding_area'] = result[carouselIndex].max_bleeding_area;
        other_data['left_iqa_score'] = result[carouselIndex].iqa_score_left;
        other_data['right_iqa_score'] = result[carouselIndex].iqa_score_right;
        other_data['other_advice'] = '';
        total_data['basic_data'] = basic_data;
        total_data['other_data'] = other_data;
        return total_data
      },

      /**/
      createReport:function(){
        let data = this.getReportData();
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        axios.post(`${host}operation/createPdf/`, data)
          .then(res=>{
            loading.close();
            if(res['data'].length>1){
              let report1=Math.random().toString(36).substr(2);
              let temWindow=window.open('',report1,'');
              temWindow.document.title="pdf";
              let patient_name = data['basic_data']['patient_name'];
              let patient_id = data['basic_data']['patient_id'];
              let url_pdf="/media/patient_pdf/"+res['data']+'/'+patient_name+"_"+patient_id+".pdf";
              temWindow.location=url_pdf;
            }else{
              this.$alert('', '', {
                confirmButtonText: '',
              });
            }
          }).catch(error=>{
          loading.close();
          console.log((error));
          this.$alert('', '', {
            confirmButtonText: '',
          });
        });
      },

      /**/
      clickCreateImgBtn:function(){
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });

        let temp_data = this.getReportData();
        let right_origin_src = temp_data['other_data']['right_origin_src'];
        let left_origin_src = temp_data['other_data']['left_origin_src'];
        let username = temp_data['other_data']['username'];
        let data = {};
        data['patient_id'] = temp_data['basic_data']['patient_id'];
        data['right_origin_src'] = right_origin_src;
        data['left_origin_src'] = left_origin_src;
        data['username'] = username;

        createImg(data).then(res=>{
          console.log('res=>',res);
          if(res===0){
            loading.close();
            this.$alert('', '', {
              confirmButtonText: '',
            });
          }else{
            loading.close();
            let report1=Math.random().toString(36).substr(2);
            let temWindow=window.open('',report1,'');
            temWindow.document.title="profession";
            temWindow.location=res;
          }
        }).catch(error=>{
          loading.close();
          this.$alert('', '', {
            confirmButtonText: '',
          });
        })

      },

      /**/
      handleSelectionChange(val) {
        this.multipleSelection = val;
        console.log(val);
      },
      /**/
      batchPdfGen:function(){
        if(this.multipleSelection.length===0){
          this.$message({
            message: '',
            type: 'error'
          });
          return
        }
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.sendBatchToServer();
          this.nominateZip();
        }).catch((e) => {
          console.log(e);
          this.$message({
            type: 'info',
            message: ''
          });
        });



      },
      /**/
      sendBatchToServer:function(){
        let data = this.multipleSelection;
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        axios.post(`${host}operation/createPdf/`, data)
          .then(res=>{
            console.log('res=>',res['data']);
            if(res['data'].length>5){
              loading.close();
              let download_zip=confirm("");
              if(download_zip){
                let path=res['data'];
                let a = document.getElementById('downloadzip');
                a.href = path;
                a.click();}
            }else{
              loading.close();
              this.$alert('', '', {
                confirmButtonText: '',
              });
            }
          }).catch(error=>{
          console.log((error));
          loading.close();
          this.$alert('', '', {
            confirmButtonText: '',
          });
        });
        },

      //
      handleView: function (index, row) {
        this.reportFormVisible = true;
        this.value = true; //
        this.reportForm = Object.assign({}, row);
        //this.getDiagnoseResult(this.reportForm.diagnose);
        let picList = this.getPictureInfo(row['picture']);
        console.log('picList',this.reportForm.diagnose);
        //AI,
        let AIdiagnose = this.reportForm.diagnose;
        let result = this.getDiagnoseResult(AIdiagnose);
        console.log('result',result);
        this.oozed_bleeding = result;

        //this.reportForm = result;
        this.picList = picList;
        let hospital = this.picList[0]['hospital'];
        let position = host + "media/"+hospital+'/';
        let srcList = [];
        for(let i=0;i<this.picList[0]['data'].length;i++){
          srcList.push(position+this.picList[0]['data'][i]);
        }
        //this.srcList = srcList;
        console.log('1324',srcList);
        let leftEyeList = [];
        let rightEyeList = [];
        for (let i=0;i<srcList.length;i++){
          // infrared()
          if(srcList[i].search('infrared') !==-1){
            console.log('i find infrared and filter it',srcList[i]);
            continue
          }
          console.log('2345',srcList[i]);
          if (srcList[i].search('Left') !==-1){
            leftEyeList.push(srcList[i]);
          }else{
            rightEyeList.push(srcList[i]);
          }
        }

        this.right_srcList = rightEyeList;
        this.left_srcList = leftEyeList;
        this.right_src = rightEyeList[0];
        this.left_src = leftEyeList[0];


        if(rightEyeList.length===0){
          console.log('');
          rightEyeList[0]=host+'media/'+'noimg.jpg';
          this.right_src=rightEyeList[0];
          this.right_srcList=[];

        }
        if(leftEyeList.length===0){
          console.log('');
          leftEyeList[0]=host+'media/'+'noimg.jpg';
          console.log(leftEyeList);
          this.left_src=leftEyeList[0];
          this.left_srcList=[];
        }

      },
      //
      closeReport:function () {
        setTimeout(() => {
         this.reportFormVisible = false;  //
        }, 100);
      },

      //
      getDiagnoseResult:function(diagnose){
        for (let i=0;i<diagnose.length;i++){
          diagnose[i]['oozed_area'] = (diagnose[i]['left_oozed_area']*10+diagnose[i]['right_oozed_area']*10)/10;
          diagnose[i]['oozed_amount'] = parseInt(diagnose[i]['left_oozed_amount'])+parseInt(diagnose[i]['right_oozed_amount']);
          diagnose[i]['max_oozed_area'] = (diagnose[i]['left_max_oozed_area']*10+diagnose[i]['right_max_oozed_area']*10)/10;

          diagnose[i]['bleeding_area'] = (diagnose[i]['left_bleeding_area']*10+diagnose[i]['right_bleeding_area']*10)/10;
          diagnose[i]['bleeding_amount'] = parseInt(diagnose[i]['left_bleeding_amount'])+parseInt(diagnose[i]['right_bleeding_amount']);
          diagnose[i]['max_bleeding_area'] = (diagnose[i]['left_max_bleeding_area']*10+diagnose[i]['right_max_bleeding_area']*10)/10;
          diagnose[i]['iqa_score_left'] = diagnose[i]['left_iqa_score']< 60 ? '' : diagnose[i]['left_iqa_score']>= 75 ? '' : '';
          diagnose[i]['iqa_score_right'] = diagnose[i]['right_iqa_score']< 60 ? '' : diagnose[i]['right_iqa_score']>= 75 ? '' : '';

        }
        console.log('',diagnose);
        return diagnose;
      },

      //,
      getPictureInfo:function (pictureList) {
        let map = {}, dest = [];
        for(let i = 0; i < pictureList.length; i++){
          let ai = pictureList[i];
          let pic_name = ai['pic_name'];
          let hospital = ai['hospital'];
          if(!map[ai.own_to]){
            dest.push({
              own_to: ai.own_to,
              hospital:hospital,
              data: [pic_name]
            });
            map[ai.own_to] = ai;
          }else{
            for(let j = 0; j < dest.length; j++){
              let dj = dest[j];
              if(dj.own_to === ai.own_to){
                dj.data.push(pic_name);
                break;
              }
            }
          }
        }
        return dest;
      },
      //,
      changeCarousel:function (callback) {
        this.carouselIndex = callback;
        let hospital = this.picList[callback]['hospital']
        let position = host + "media/"+hospital+'/';
        let srcList = [];
        for(let i=0;i<this.picList[callback]['data'].length;i++){
          srcList.push(position+this.picList[callback]['data'][i]);
        }
        //this.srcList = srcList;
        console.log('1324567',srcList);
        let leftEyeList = [];
        let rightEyeList = [];
        for (let i=0;i<srcList.length;i++){
          // infrared()
          if(srcList[i].search('infrared') !==-1){
            console.log('i find infrared and filter it',srcList[i]);
            continue
          }
          if (srcList[i].search('Left') !==-1){
            leftEyeList.push(srcList[i]);
          }else{
            rightEyeList.push(srcList[i]);
          }
        }

        /*list */
        leftEyeList = leftEyeList.reverse();
        rightEyeList = rightEyeList.reverse();

        this.right_src = rightEyeList[0];
        this.left_src = leftEyeList[0];
        this.right_srcList = rightEyeList;
        this.left_srcList = leftEyeList;
        if(rightEyeList.length===0){
          console.log('');
          rightEyeList[0]=host+'media/'+'noimg.jpg';
          this.right_src=rightEyeList[0];
          this.right_srcList=[];
        }
        if(leftEyeList.length===0){
          console.log('');
          leftEyeList[0]=host+'media/'+'noimg.jpg';
          console.log(leftEyeList);
          this.left_src=leftEyeList[0];
          this.left_srcList=[];
        }
      },
      nominateZip:function(){
    var a = document.getElementById("downloadzip");
    var time = this.getNowFormatDate();
    a.setAttribute("download",time+".zip");
  },
      getNowFormatDate:function() {
        let date = new Date();
        let seperator1 = "";// //
        let seperator2 = "_";// ::
        let month = date.getMonth() + 1;//
        let strDate = date.getDate();//
    if (month >= 1 && month <= 9)
    {
      month = "0" + month;
    }
    if (strDate >= 0 && strDate <= 9)
    {
      strDate = "0" + strDate;
    }
        let currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate
      + "/" + date.getHours() + seperator2 + date.getMinutes()
      + seperator2 + date.getSeconds();
    return currentdate;
  }

    },
    data() {
      let validateTel = (rule, value, callback) => {
        let telLength = value.length;
        if (telLength !==11 ) {
          callback(new Error('11'));
        } else {
          callback();
        }
      };

      let validateIDnumber = (rule, value, callback) => {
        if(value){
          let telLength = value.length;
          if (telLength !==18 ) {
            callback(new Error('18'));
          } else {
            callback();
          }
        }else{
          callback();
        }
      };

      let validateName = (rule, value, callback) => {
        let nameLength = value.length;
        let reg = /^[\u4E00-\u9FA5A-Za-z0-9_]+$/;
        if (nameLength >10 ) {
          callback(new Error('10'));
        }else if(!reg.test(value)){
          callback(new Error(''));
        }
        else {
          callback();
        }

      };

      let validateAddress = (rule, value, callback) => {
        let nameLength = value.length;
        if (nameLength >30 ) {
          callback(new Error('30'));
        } else {
          callback();
        }
      };

      let validateAge = (rule, value, callback) => {
        if (value<1) {
          callback(new Error(''));
        } else {
          callback();
        }
      };
      return {
        carouselIndex:0,
        pregnant_seen:false,
        oozed_bleeding:[],
        picList:[],
        tableData: [],
        multipleSelection: [],
        dateValue:'',
        isActive1:true,
        isActive2:false,
        isActive3:false,
        isActive4:false,
        currentPage: 1,
        inputVal:'',
        value: true,
        page_size:10,
        totalNum:30,
        otherDate:false,
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        editFormVisible: false,//
        reportFormVisible: false,//
        editLoading: false,
        editFormRules: {
          patient_name: [
            { required: true, message: '', trigger: 'change' },
            { validator: validateName, trigger: 'change' },
          ],
          patient_tel: [
            { required: true, message: '', trigger: 'change' },
            { validator: validateTel, trigger: 'change' },
          ],
          patient_age: [
            { required: true, message: '', trigger: 'blur' },
            { validator: validateAge, trigger: 'blur' },
          ],
          ID_number: [
            { validator: validateIDnumber, trigger: 'change' },
            {
              pattern: /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/,
              message: '',
              trigger: 'change'
            }
          ],
          address: [
            { validator: validateAddress, trigger: 'change' },
          ],
        },
        //
        editForm: {
          patient_id: 0,
          patient_name: '',
          patient_gender: '0',
          patient_age: 0,
          patient_tel:18888888888,
          ID_number:'',
          birth: '',
          jwsRadio:0,
          jwsExist:0,
          jwsVisible:false,
          isFront:true,
          address: '',
        },
        editFormJwsForm:{
          other:'',
          DM_year:'', //
          HTN_year:'', //
          HPL_year:'', //
          pregnant_weeks:'',
        },
        radio:3,
        reportForm:{
          patient_id: 0,
          patient_name: '',
          patient_gender: '0',
          patient_age: 0,
          patient_tel:18888888888,
          address: '',
          diagnose:[]
        },
        editFormLeftDiopter: [{
          value: '0',
          label: ''
        },{
          value: '1',
          label: '(75~300)'
        }, {
          value: '2',
          label: '(300~600)'
        }, {
          value: '3',
          label: '(>600)'
        }, {
          value: '4',
          label: ''
        }],
        editFormRightDiopter: [{
          value: '0',
          label: ''
        }, {
          value: '1',
          label: '(75~300)'
        }, {
          value: '2',
          label: '(300~600)'
        }, {
          value: '3',
          label: '(>600)'
        }, {
          value: '4',
          label: ''
        }],
        editFormLeftDiopterValue: '',
        editFormRightDiopterValue: '',
        left_src: '',
        right_src:'',
        left_srcList: [],
        right_srcList:[],
      }
    }
  }

</script>

<style scoped>
  .button_area{
    display:flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
  }

  .undiagnosed{
    text-align: left;
    margin-top: 20px;
  }
  .timezone,.dataZone{
    margin-top: 20px;
  }
  .el-date,.el-icon-date,.icon-shuaxin1{
    color: dodgerblue;
  }

  .operationNew{
    margin-top: 20px;
    text-align: center;
    line-height: 41px;
  }
  a{
    text-decoration: none;
    color: dodgerblue;
  }

  .el-input__inner{
    border: dodgerblue 1px solid;
    color: dodgerblue;
  }

  .active{
    border: 1px solid dodgerblue;
    border-radius: 4%;
  }

  /*.active>>> a{*/
  /*  color: #FFFFFF;*/
  /*  */
  /*}*/
  /*.active >>> i{*/
  /*  color: #FFFFFF;*/
  /*}*/

  .pagination{
    margin-top: 20px;
    text-align: right;
  }

  .el-date-editor.el-input, .el-date-editor.el-input__inner{
    width: 140px;
  }

  .timepicker >>> i{
    color: dodgerblue;
    font-size: x-large;
  }
  .timepicker >>> input {
    border: none;
    font-size: 15px;
  }

  .timepicker >>>input::-webkit-input-placeholder{
    color: dodgerblue;
  }

  .timepicker >>>input::-moz-placeholder{
    color: blue;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #efefef;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #efefef;
  }
</style>




